@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@600&display=swap);
* {
  text-align: center;
  font-family: "Roboto Mono";
}

body {
  background-color: #2d2d34 !important;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  overflow-y: hidden;
}

html body {
  padding: 0;
  margin: 0;
  overflow-y: inherit;
}

.title {
  margin-top: 3rem;
  color: #f9c80e;
  font-size: 2rem;
}

.element {
  position: relative;
  display: inline-block;
  background-color: #87a9ab;
  margin-top: 5vh;
  margin-left: 5px;
  margin-right: 5px;
  vertical-align: bottom;
}

.hidden-element {
  position: relative;
  display: inline-block;
  background-color: #87a9ab;
  margin-top: 5vh;
  margin-left: 5px;
  margin-right: 5px;
  vertical-align: bottom;
}

.wrapper {
  margin-top: 5vh;
  width: 75vw;
  height: 60vh;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.infoWrapper {
  width: 100%;
  height: 15vh;
  position: absolute;
  bottom: 0;
  padding: 10px;
}

button {
  background-color: #f9c80e;
  border: none;
  color: #2d2d34;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  display: inline-block;
}

.sort-button {
  margin: 5px;
}

a,
.navbar-nav,
.navbar-light .nav-link {
  color: #f9c80e !important;
}

.navbar-light .nav-link:hover {
  color: #c60f7b !important;
}

.navbar-brand {
  font-size: 1.4em;
  color: #f9c80e !important;
}
.navbar-brand:hover {
  color: #f2f0e3 !important;
}

.info {
  color: #f9c80e;
  text-align: left;
  margin-left: 10px;
  margin-top: 10px;
  font-size: 1.2em;
  line-height: 50%;
}

.nav-link[disabled] {
  pointer-events: none;
  color: #c5bfbf !important;
}

button[disabled] {
  pointer-events: none;
  background-color: #c5bfbf;
  color: black;
}

button:hover {
  background-color: #c5bfbf;
}

button:focus {
  background-color: #f9c80e;
}

.slider-bar {
  margin-left: 10px;
  width: 20% !important;
  display: inline-block;
}

.numberText {
  position: absolute;
  bottom: 0;
  text-align: center;
  width: 100%;
  padding-bottom: 10px;
}

.app {
  width: 90vw;
  margin-left: auto;
  margin-right: auto;
}

.navbar {
  padding: 20px !important;
}

.navbar-brand:hover {
  color: #f9c80e !important;
}

.navbar-hamburger {
  border: none !important;
}

.navbar-hamburger:focus {
  background-color: transparent;
  border: none !important;
}

.navbar-hamburger:hover {
  background-color: transparent;
  border: none !important;
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .title {
    margin-top: 50px;
    color: #f9c80e;
    font-size: 1.5rem;
  }
  .wrapper {
    margin-top: 0;
    margin-bottom: 5vh;
  }
  .app {
    width: 100vw;
  }
  button:hover {
    background-color: #f9c80e;
  }
  .slider-bar {
    width: 50vw !important;
  }
}

